<template lang="pug">
  draggable.cards-board(:list="cards" :group="group" :class="{ 'border-line': border }")
    div(v-for="card in cards" 
      :key="card.index"
      :class="['small-card','color'+card.catIndex, { 'disabled': card.disabled, 'active': actionEnable }]"
      @click="$emit('click', card)"
    )
      | {{ card.fullRent ? '*' : '' }}
      | {{ card.name.slice(0, maxLen) }}
      | {{ card.name.length > maxLen ? '...' : '' }}
      | {{ card.houses>0 && card.houses<5 ? ''+card.houses : '' }}
      | {{ card.houses===5 ? 'c .' : '' }}
</template>

<script>
import draggable from 'vuedraggable'
export default {
  name: 'CardsList',
  props: [ 'cards', 'actionEnable', 'group', 'border' ],
  components: {
    draggable,
  },
  computed: {
    maxLen(){
      return this.cards.length > 12 ? 5 : 10;
    }
  },
}
</script>

<style lang="scss" scoped>
  .disabled{
    text-decoration: line-through;
  }
  .active{
    cursor: pointer;
  }
  .small-card{
    box-sizing: border-box; 
    border: 1px solid rgb(105, 16, 16);
    height: 20px;    
  }
  .cards-board{   
    margin: 0 auto;
    width: 300px;
    height: 80px;
    font-size: 12px;
    display: flex;
    flex-flow: column wrap;
    justify-content: left;     
  }
  .border-line{
    border: 1px solid #300;
  }
</style>