<template lang="pug">
  #swap-board
    ul(v-if="!tradePlayer")
      li(v-for="player in players" :key="player.id") 
        a(@click.prevent="selectPlayer(player)" href="#") {{ player.name }}
    div(v-else)
      div {{ tradePlayer.name }} / {{ tradePlayer.cash }}
      div : 
        input(v-model="tradePlayerCash" :disabled="tradePlayerAccept")
        button(@click="accept('tradePlayer')") {{ tradePlayerAccept ? '' : '' }}
      div :
        CardsList(:cards="tradePlayerCards", :actionEnab="true", group="tradePlayer", :border="true", v-show="!tradePlayerAccept")
      div  :
        CardsList.bg(:cards="tradePlayerCardsSelected", :actionEnab="true", group="tradePlayer", :border="true")
      div //
      div  :
        CardsList.bg(:cards="currentPlayerCardsSelected", :actionEnab="true", group="currentPlayer", :border="true")
      div :
        CardsList(:cards="currentPlayerCards",  :actionEnab="true", group="currentPlayer", :border="true" v-show="!currentPlayerAccept")
      div : 
        input(v-model="currentPlayerCash" :disabled="currentPlayerAccept")
        button(@click="accept('currentPlayer')") {{ currentPlayerAccept ? '' : '' }}
      div {{ currentPlayer.name }} / {{ currentPlayer.cash }}

    button(@click="switchSwap")    
</template>

<script>
import { mapMutations, mapState, mapGetters, mapActions } from 'vuex';
import draggable from 'vuedraggable';
import CardsList from './CardsList';

export default {
  name: 'SwapBoard',
  components:{
    draggable,
    CardsList,
  },
  data(){
    return{
      tradePlayer: null,
      tradePlayerCards: [],
      tradePlayerCardsSelected: [],
      tradePlayerCash: 0,
      tradePlayerAccept: false,
      currentPlayerCards: [],
      currentPlayerCardsSelected: [],
      currentPlayerCash: 0,
      currentPlayerAccept: false
    }
  },
  computed:{
    ...mapState(['game']),
    ...mapGetters(['currentPlayer']),
    players(){
      return this.game.players.filter(plr=>plr.id!==this.game.whoGo);
    }
  },
  methods: {
    ...mapMutations(['switchSwap']),
    ...mapActions(['swapCard']),
    selectPlayer(player){
      this.tradePlayer = player;
      this.tradePlayerCards = this.game.board.filter(card=>card.owner===player.id);  
      this.currentPlayerCards = this.game.board.filter(card=>card.owner===this.currentPlayer.id);    
    },
    accept(prefix) { 
      console.log(this.tradePlayerCardsSelected); 
      console.log(this.currentPlayerCardsSelected);  
      if(this[prefix+'Cash']>this[prefix].cash){
        return;
      }
      this[prefix+'Accept'] = !this[prefix+'Accept'];
      if(this.tradePlayerAccept && this.currentPlayerAccept){
        const result = {};
        result.saler = { player: this.tradePlayer, cards: this.tradePlayerCardsSelected, cash: this.tradePlayerCash };
        result.customer = { player: this.currentPlayer, cards: this.currentPlayerCardsSelected, cash: this.currentPlayerCash };
        this.swapCard(result);
        this.switchSwap();
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  #swap-board {
    margin-top: 10px;
  }    
  .bg{    
    background: lightgray;
  }
  .top10{
    margin-top:10px;
  } 
  input{
    margin: 5px 0;
  }
</style>