<template lang="pug">
  .card
    div.m-top {{ currentPlayer.name }} / {{ currentPlayer.cash }}
    .card-info
      .card-header(v-if="card.category" :class="['color'+card.catIndex, { 'disabled': card.disabled } ]") 
        | {{ card.fullRent ? '*' : '' }} 
        | {{ card.category }} 
        | {{ card.houses>0 && card.houses<5 ? ''+card.houses : '' }}
        | {{ card.houses===5 ? 'c .' : ''}}
      .card-body
        div {{ card.name }}
        div : {{ card.cost }}
        div
          ul
            li(v-for="(rent, index) in card.rent" :key="rent")
              span(v-if="index===0") : {{ rent }}
              span(v-if="index===1") 1 : {{ rent }}
              span(v-if="index>1 && index<5") {{ index }} : {{ rent }} 
              span(v-if="index===5")  : {{ rent }}
        div(v-if="!card.disabled") : {{ card.deposit }}
        div(v-else) : {{ card.buyout }}
        div  : {{ card.buildCost }}
    .card-actions
      button(v-if="!card.disabled" @click="toLay(card.index)") 
      button.m-top(v-else @click="redeem(card.index)") 
      button.m-top(@click="byeHouse(card)" )   / 
      button.m-top(@click="saleHouse(card)" v-if="card.houses > 0" )  
      button.m-top(@click="clearSelectedCard") 


</template>

<script>
import { mapActions, mapMutations, mapGetters } from 'vuex';
export default {
  name: 'Card',
  props: ['card'],
  computed: {
    ...mapGetters(['currentPlayer']),
  },
  methods:{
    ...mapMutations(['clearSelectedCard']),
    ...mapActions(['toLay', 'redeem', 'byeHouse', 'saleHouse'])
  }
}
</script>

<style lang="scss" scoped>
  .card-actions{
    button{
      display: block;
      margin: 0 auto;
    }
  }
  .m-top{
    margin-top: 5px !important;
  }
  .card-info{
    margin: 10px auto;
    height: 350px;
    width: 220px;
    border: 2px solid #300;
    .card-header{
      line-height: 40px;
      border-bottom: 2px solid #300;
    }
    .card-body{
      margin: 10px 0 0 0;
    }
  }
  .disabled{
    text-decoration: line-through;
  }

  $colors: (
    1: #8819c9,
    2: #10a2b6,
    3: #ce1e67,
    4: #c76413,
    5: #dd0000,
    6: #ffee00,
    7: #1eb400,
    8: #2f0ee9,
    100: #723939,
    200: #4d5f88,
    1000: #467050
  );

  @each $number, $color in (0, #ff484846), (1, #3939ff31), (2, #92dd9252), (3, #ffa60054){
      .owner-color#{$number}{
        background-color: $color;
      }
  }

  @each $n, $color in $colors {
    .color#{$n}{
      @if $n==6 {color: #300300;}
      @if $n!=6 {color: white;}
      background: $color;
      cursor: pointer;     
    }
  }
</style>