<template lang="pug">
  div.player-cards  :
    CardsList(:cards="playerCards" :maxLen="10" :actionEnable="true" @click="selectCard($event)")
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import CardsList from './CardsList'

export default {
  name: 'PlayerCards',
  components: {
    CardsList,
  },
  computed: {
    ...mapGetters(['playerCards', 'selectedCard']),
    cardNameSlice(){
      if(this.playerCards.length<=16){
        return 10;
      } else {
        return 5;
      }
    }
  },
  methods: {
    ...mapMutations(['selectCard']),    
  }
}
</script>

<style lang="scss" scoped>
  .player-cards{
    margin-top: 10px;
  }
</style>