<template lang="pug">
  .player-info
    ul.player-list
      li(v-for="player in game.players" :key="player.id" :class="activeElementStyle(player)") {{ player.name }} / cash: {{ player.cash }}
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'PlayerInfo',
  computed: {
    ...mapState(['game']),
    ...mapGetters(['currentPlayer'])
  },
  methods: {    
    activeElementStyle(player){
      let resultClass = {'game-over': player.gameOver};
      if(player.id === this.currentPlayer.id){
        Object.assign(resultClass, {'active': true, ['player'+player.index]: 'true'})
      }
      return resultClass;
    }
  }
}
</script>

<style lang="scss" scoped>
  .player-info{
    margin-top: 10px;
  }
  .player-list{
    width: 300px;
    padding: 0;
    margin: 0 auto;    
    list-style-type: none;
    color: #300300be;
    .game-over{
      text-decoration: line-through;
    }
    @each $number, $color in (0, #ff4848), (1, #3939ff), (2, #92dd92), (3, #ffa500){
      li.active.player#{$number}{
        color: #300300;
        &::before{
          content: '';
          margin: 0 5px 0 -10px;        
          display: inline-block;
          height: 5px;
          width: 5px;
          border-radius: 50%;
          border: 1px solid #300300;          
          background: $color;
        }
      }
    }    
  }  
  
</style>