<template lang="pug">
  div
    section.start-screen(v-if="!gameStarted")
      p(v-if="winner") : {{ winner.name }}  
      div(v-else) 
        p         
        p  
        div(@click='startGame(2)') (2 )
        div(@click='startGame(3)') (3 )
        div(@click='startGame(4)') (4 )

    section.game-board(v-else)
      board
      main-card
      div(v-if="game.auction")
        div 
        div  : {{ game.auction.bet }}
        div  : {{game.auction.current.name}}
        input(ref="bet")
        button(@click="accept") V
        button(@click="cancelAuc") X
      div(v-else)      
        .actions        
            button(@click="startTurn" :disabled="game._action || !game._endOfTurn")    
            button(@click="doAction" :disabled="!game._action")        
            button(@click="endTurn" :disabled="game._action || game._endOfTurn") 
            button(@click="switchSwap" :disabled="game._endOfTurn") 
            button(@click="dontBuy" v-show="game._action && currentCard.canBuy")          
            button(@click="payFine" v-show="checkJail")     50         
            button(@click="checkWinner") !
        div {{ game.cubes }}
        player-info      
      player-cards
    .modal(:style="{ display: selectedCard || swap ? 'block' : 'none' }")
      .game-board
        Card(v-if="selectedCard" :card="selectedCard")
        SwapBoard(v-if="swap")

</template>

<script>

import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
import Board from './components/Board';
import PlayerInfo from './components/PlayerInfo';
import PlayerCards from './components/PlayerCards';
import MainCardVue from './components/MainCard.vue';
import Card from './components/Card';
import SwapBoard from './components/SwapBoard';

export default {
  components:{
    board: Board,
    "player-info": PlayerInfo,
    "player-cards": PlayerCards,
    "main-card": MainCardVue,
    Card,
    SwapBoard
  },  
  computed: {
    ...mapState(['gameStarted', 'game', 'selectedCard', 'swap', 'winner']),
    ...mapGetters(['currentPlayer', 'currentCard', 'getOwner', 'playerCards', 'checkJail'])
  },
  methods: {
    ...mapActions(['startTurn', 'doAction', 'endTurn', 'dontBuy', 'acceptAuc', 'cancelAuc', 'payFine', 'gameOver']),
    ...mapMutations(['startGame', 'switchSwap', 'win']),
    accept(){
      this.acceptAuc(this.$refs.bet.value);
    },  
    checkWinner(){
      this.gameOver(this.currentPlayer.id);
      if(this.game.winner){
        this.win(this.game.winner);
      }
    }
  },
}
</script>

<style lang="scss">  
  .modal{
    background: url('./assets/img/1580653594_3-p-foni-s-teksturoi-pergamentnogo-lista-9.jpg');
    height: 100%;
    width: 100%;
    position:absolute;
    left: 0;
    top: 0;
    z-index: 9999;
    //display: none;
    .modal-box{
      
    }
  }
  .actions{
    margin: 10px auto;
    display: flex;
    flex-flow: column wrap;
    height: 60px;
    max-width: 350px;
  }
  body{
    margin: 0;
    padding: 0;
    color: #300300;
    background: Url('./assets/img/1580653594_3-p-foni-s-teksturoi-pergamentnogo-lista-9.jpg');
    font-family: 'Calibri';
    background-size: auto;
  }
  section{
    width: 100%;
  }
  .start-screen{
    text-align: center;
  }
  .game-board{
    display: flex;
    text-align: center;
    flex-direction: column;
  } 
  ul{
    list-style-type: none;
    padding: 0;
  }
  button{
    font-weight: 700;
    border: none;
    background: none;
    text-decoration: underline;
    color: #300;
    &:disabled{
      color: #300300be;
    }
  }

  $colors: (
    1: #8819c9,
    2: #10a2b6,
    3: #ce1e67,
    4: #c76413,
    5: #dd0000,
    6: #ffee00,
    7: #1eb400,
    8: #2f0ee9,
    100: #723939,
    200: #4d5f88,
    1000: #467050
  );

  @each $n, $color in $colors {
    .color#{$n}{
      @if $n==6 {color: #300300;}
      @if $n!=6 {color: white;}
      background: $color;
      //border: 1px solid #300;
      cursor: pointer;     
    }
  }

</style>
